import { Component, OnInit } from "@angular/core";
import { initImageAnimation } from "../../share/animations/initImage-animations";
import { listAnimation } from "../../share/animations/listAnimation-animations";

@Component({
    selector:'user-index',
    templateUrl:'./index.component.html',
    styleUrls:['./index.component.css'],
    animations:[initImageAnimation,listAnimation]
})

export class UserIndexComponent implements OnInit{

    initState:string = 'in';
    mockData1:Array<any> = [
        {label:'模块一',goal:'person-info'},
        {label:'模块二',goal:'person-company'},
        {label:'模块三',goal:'person-work'},
        {label:'模块四',goal:'person-skill'},
        {label:'模块五',goal:'person-say'}]
    mockData = [];
    ngOnInit() {
        let self = this;
        setTimeout(function() {
            self.initState = 'out';
        },500)
        setTimeout(function() {
            self.mockData = self.mockData1;
        },2500)
    }
}


